.modal {
    display: none;
    position: fixed;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.25);
    display: flex;
  }
  
  .modal_content {
    background-color: rgb(247, 247, 247);
    position: absolute;
    top: 20%;
    left: 30%;
    width: 40%;
    padding: 20px;
    border-radius: 5px;
    border: 2px solid black;
  }
  
  .close {
    color: Black;
    position: absolute;
    top: 2%;
    right: 98%;
  }
  
  .close:hover {
    color: red;
    cursor: pointer;
  }